.portal {
  width: 375px;
  height: 375px;
  margin-left: 800px;
  border: 7px solid rgb(212, 184, 255);
  margin-top: 0px;
  background-color: black;
  border-radius: 50%; /* Ensures the element is a circle */
}

.overlap {
  background-color: rgb(212, 184, 255);
}

.feedback1 {
  position: fixed;
  bottom: 30px;
  right: 30px;
  text-align: center;
  padding: 10px 40px;
  border: none;
  border-radius: 7px;
  background-color: rgb(211, 234, 255);
  font-size: x-large;
  font-family: "Thunder";
}

.feedback1:hover {
  position: fixed;
  bottom: 30px;
  right: 30px;
